<template>
  <v-dialog v-model="dialog.show" persistent max-width="880px">
    <v-card style="background-color: #ECEFF1">
      <v-card-title> </v-card-title>
      <v-form @submit.prevent="save">
        <v-card max-width="820px" class="ml-6">
          <v-card-text>
            <v-container>
              <v-row wrap>
                <v-col cols="12" sm="4" class="mb-0 py-0">
                  <v-autocomplete
                    v-model="form.doctor"
                    :items="itemsDoctor"
                    :loading="isLoadingDoctor"
                    :search-input.sync="searchDoctor"
                    color="#007bff"
                    item-text="name"
                    item-value="id"
                    :label="$vuetify.lang.t('$vuetify.doctor.title')"
                    class="font-weight-bold"
                    clearable
                  ></v-autocomplete>
                </v-col>
                <v-col cols="12" sm="8" class="mb-0 py-0">
                  <v-select
                    v-model="form.intervention_days"
                    color="#007bff"
                    :items="optionDays.type_Days"
                    :label="
                      $vuetify.lang.t(
                        '$vuetify.doctor.cols.intervention_days.title'
                      )
                    "
                    :placeholder="
                      $vuetify.lang.t('$vuetify.doctor.cols.role.title2')
                    "
                    class="font-weight-bold"
                    multiple
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-divider class="mx-4" style="margin-top: -25px"></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="dialog.show = false">
              {{ $vuetify.lang.t("$vuetify.btn.close") }}
            </v-btn>
            <v-btn color="#007bff" :loading="loading" dark type="submit">
              {{ $vuetify.lang.t(messages.submit) }}
            </v-btn>
          </v-card-actions>
        </v-card>
        <v-card-text> </v-card-text>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
import FormMixin from "./../../mixins/Common/Form.vue";

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          intervention_days: null,
          doctor: null
        };
      }
    }
  },
  data: () => ({
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    messagesDelete: {
      success: "$vuetify.appointment.cols.cancel_appointment.success"
    },
    dialogDelete: {
      show: false
    },
    optionDays: {
      type_Days: []
    },
    menu: false,
    urlItems: "doctors/schedules",
    urlDoctors: "doctors",
    urlDeleteItems: "",
    isLoadingDoctor: false,
    searchDoctor: null,
    itemsDoctor: []
  }),

  watch: {
    searchDoctor(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlDoctors,
          params: params
        })
        .then(response => {
          console.log("respp", response.data.content);
          self.itemsDoctor = response.data.content;
        })
        .finally(() => (this.isLoading = false));
    }
  },
  created() {
    this.getOptionsJour();
  },
  validations() {
    let validators = {
      form: {}
    };
    return validators;
  },
  methods: {
    getOptionsJour() {
      // On pouvait aussi utliser le forEach au lieu de   _.eaach
      let self = this;
      this._.each(this.$store.getters.days, type_Days => {
        self.optionDays.type_Days.push({
          text: self.$vuetify.lang.t(type_Days.text),
          value: type_Days.value
        });
      });
    },
    createD() {
      this.dialogForm.show = true;
      this.formData = {
        id: null,
        doctor: null,
        intervention_days: null
      };
    },
    // Fonction pour rcuperer les types de roles dans la store

    // Fonction pour rcuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        doctor: this.form.doctor,
        intervention_days: this.form.intervention_days
      };
      return data;
    }
  },
  computed: {
    // Gestion des erreurs du formulaire
  },
  components: {}
};
</script>
