<template>
  <div>
    <v-card class="pb-4" height="60">
      <div class="mt-5 mx-5">
        <template v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])">
          <v-btn class="mr-2 my-2" @click="create()" color="#007bff" dark>
            <v-icon left>mdi-calendar-check</v-icon>
            {{ $vuetify.lang.t("$vuetify.btn.add") }}
          </v-btn>
        </template>
        <v-btn
          :loading="loading.refresh"
          @click="refreshItems()"
          class="mr-2 my-2"
          depressed
          color="#f1f5fc"
        >
          <v-icon left color="#007bff">mdi-refresh</v-icon>
          {{ $vuetify.lang.t("$vuetify.btn.refresh") }}
        </v-btn>
        <v-spacer></v-spacer>
      </div>
    </v-card>
    <v-card class="mt-5">
      <v-card-text>
        <v-row>
          <div>
            <div>
              <template>
                <div
                  class="call-button-container"
                  style="background-color: #29A3D8"
                >
                  {{ $vuetify.lang.t("$vuetify.days.MONDAY").toUpperCase() }}
                </div>
                <div
                  class="subtitle-2 mt-2 call-button-container"
                  :key="item.name"
                  v-for="item in monday"
                  style="background-color: #29A3D8"
                >
                  {{ item.name.toUpperCase() }}
                </div>
              </template>
            </div>
          </div>
          <div class="ml-4">
            <template>
              <div
                class="call-button-container"
                style="background-color: #007bff"
              >
                {{ $vuetify.lang.t("$vuetify.days.TUESDAY").toUpperCase() }}
              </div>
              <div
                class="subtitle-2 mt-2 call-button-container"
                :key="item.name"
                v-for="item in tuesday"
                style="background-color: #007bff"
              >
                {{ item.name.toUpperCase() }}
              </div>
            </template>
          </div>
          <div class="ml-3">
            <template>
              <div
                class="call-button-container"
                style="background-color: green"
              >
                {{ $vuetify.lang.t("$vuetify.days.WEDNESDAY").toUpperCase() }}
              </div>
              <div
                class="subtitle-2 mt-2 call-button-container"
                :key="item.name"
                v-for="item in wednesday"
                style="background-color: green"
              >
                {{ item.name.toUpperCase() }}
              </div>
            </template>
          </div>
          <div class="ml-3">
            <template>
              <div
                class="call-button-container"
                style="background-color: #0984e3"
              >
                {{ $vuetify.lang.t("$vuetify.days.THURSDAY").toUpperCase() }}
              </div>
              <div
                class="subtitle-2 mt-2 call-button-container"
                :key="item.name"
                v-for="item in thursday"
                style="background-color: #0984e3"
              >
                {{ item.name.toUpperCase() }}
              </div>
            </template>
          </div>
          <div class="ml-3">
            <template>
              <div
                class="call-button-container"
                style="background-color: indigo"
              >
                {{ $vuetify.lang.t("$vuetify.days.FRIDAY").toUpperCase() }}
              </div>
              <div
                class="subtitle-2 mt-2 call-button-container"
                :key="item.name"
                v-for="item in friday"
                style="background-color: indigo"
              >
                {{ item.name.toUpperCase() }}
              </div>
            </template>
          </div>
          <div class="ml-4">
            <template>
              <div class="call-button-container" style="background-color: grey">
                {{ $vuetify.lang.t("$vuetify.days.SATURDAY").toUpperCase() }}
              </div>
              <div
                class="subtitle-2 mt-2 call-button-container"
                :key="item.name"
                v-for="item in saturday"
                style="background-color: grey"
              >
                {{ item.name.toUpperCase() }}
              </div>
            </template>
          </div>
          <div class="ml-4">
            <template>
              <div
                class="call-button-container"
                style="background-color: orange"
              >
                {{ $vuetify.lang.t("$vuetify.days.SUNDAY").toUpperCase() }}
              </div>
              <div
                class="subtitle-2 mt-2 call-button-container"
                :key="item.name"
                v-for="item in sunday"
                style="background-color: orange"
              >
                {{ item.name.toUpperCase() }}
              </div>
            </template>
          </div>
        </v-row>
      </v-card-text>
    </v-card>
    <form-schedule
      :dialog="dialogForm"
      :form="form"
      :messages="messages"
      @getItems="getItems"
      ref="scheduleForm"
    ></form-schedule>
  </div>
</template>

<script>
import FormSchedule from "./Form";
export default {
  data: () => ({
    itemsPerPage: 10,
    urlItems: "/doctors",
    sortBy: ["id"],
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    dialogDelete: {
      show: false
    },
    dialogDetail: {
      show: false
    },
    dialogEnt: {
      show: false
    },
    form: {
      id: null,
      code_internal: null,
      coef: null,
      dateNaiss: null,
      dateService: null,
      intervention_days: null,
      position: null,
      address: null,
      speciality: null,
      phone: null,
      email: null,
      department: null
    },
    filter: {
      username: null,
      role: null
    },
    loading: {
      list: false,
      refresh: false,
      filter: false
    },
    messages: {
      title: "",
      submit: "",
      success: ""
    },
    messagesDelete: {
      success: "$vuetify.doctor.delete.success"
    },
    monday: [],
    tuesday: [],
    wednesday: [],
    thursday: [],
    friday: [],
    saturday: [],
    sunday: []
  }),
  mounted() {
    this.getItems();
  },
  methods: {
    create() {
      this.form.id = null;
      this.form.problem = null;
      this.form.patient = null;
      this.form.start_appointment_date = null;
      this.form.department = null;
      this.form.end_appointment_date = null;
      this.form.postpone = false;
      this.form.doctor = null;
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.appointment.new.title",
        submit: "$vuetify.appointment.new.submit",
        success: "$vuetify.appointment.new.success"
      };
    },
    getItems() {
      let self = this;
      return new Promise((resolve, reject) => {
        this.loading.list = true;
        self.$store
          .dispatch("request", {
            url: self.urlItems
          })
          .then(response => {
            this.monday = [];
            this.tuesday = [];
            this.wednesday = [];
            this.thursday = [];
            this.friday = [];
            this.saturday = [];
            this.sunday = [];
            let data = response.data;
            self.items = data.content;
            const intervention_day = response.data.content;
            for (const item of intervention_day) {
              if (item.intervention_days.includes("MONDAY")) {
                const name = item.name;
                this.monday.push({
                  name
                });
              }
              if (item.intervention_days.includes("TUESDAY")) {
                const name = item.name;
                this.tuesday.push({
                  name
                });
              }
              if (item.intervention_days.includes("WEDNESDAY")) {
                const name = item.name;
                this.wednesday.push({
                  name
                });
              }
              if (item.intervention_days.includes("THURSDAY")) {
                const name = item.name;
                this.thursday.push({
                  name
                });
              }
              if (item.intervention_days.includes("FRIDAY")) {
                const name = item.name;
                this.friday.push({
                  name
                });
              }
              if (item.intervention_days.includes("SATURDAY")) {
                const name = item.name;
                this.saturday.push({
                  name
                });
              }
              if (item.intervention_days.includes("SUNDAY")) {
                const name = item.name;
                this.sunday.push({
                  name
                });
              }
            }
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data.content[0]);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loading.list = false;
            resolve();
          });
      });
    },
    showFilter() {
      this.dialogForm.shows.showFilter = true;
    },
    performFilter() {
      let self = this;
      self.loading.list = true;
      let params = {};
      if (self.filter.username !== null) {
        params["username"] = self.filter.username;
      }
      if (self.filter.role !== null) {
        params["role"] = self.filter.role;
      }
      self._search = params;
      //console.log("FILTRE", this._search);
      self
        .getItems()
        .then(() => {
          self.$refs.filterForm.closeDialog();
        })
        .finally(() => {
          self.$refs.filterForm.stopLoadingBtn();
        });
    },
    assignEnterprise(item) {
      this.dialogEnt.show = true;
      this.$refs.assignForm.setForm(item);
    }
  },
  components: {
    FormSchedule
  }
};
</script>

<style scoped>
.action {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.call-button-container {
  margin: auto;
  width: 170px;
  vertical-align: middle;
  padding: 10px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  font-family: lato-bold;
  font-size: 17px;
  color: white;
}
</style>
